import * as React from 'react';
import { ThemeProvider } from 'styled-components';
import { Dark, Light } from '../../Styles/Theme/index.styles';
export const CustomThemeContext = React.createContext({
    theme: {},
    setTheme: () => { },
    ChangeTheme: () => { }
});
const CustomThemeProvider = ({ children }) => {
    const [theme, setTheme] = React.useState(Dark);
    const ChangeTheme = () => {
        if (theme.title === 'dark') {
            setTheme(Light);
            localStorage.setItem('themeImageTracker', 'light');
        }
        else {
            setTheme(Dark);
            localStorage.setItem('themeImageTracker', 'dark');
        }
    };
    React.useEffect(() => {
        setTheme(localStorage.themeImageTracker === 'light' ? Light : Dark);
    }, []);
    return (React.createElement(CustomThemeContext.Provider, { value: { theme, setTheme, ChangeTheme } },
        React.createElement(ThemeProvider, { theme: theme }, children)));
};
export default CustomThemeProvider;
